<template>
	<v-container>
		<h1 style="text-align: center; margin-bottom: 20px; color: #009688; text-decoration: underline">Country Selector Built Using Vue.js</h1>
		<v-row>
			<v-spacer></v-spacer>
			<v-col cols="12" lg="4">
				<v-autocomplete
					v-model="selected"
					:items="countries"
					item-text="text"
					item-value="id"
					label="Locations"
					color="#009688"
					outlined
					multiple
					chips
					deletable-chips
					small-chips
					return-object
					@focus="expand=false"
					ref="continentSelector"
				>
					<template slot="prepend-item">
						<div class="text-center">
							<v-btn rounded @click= "showContinentSelector" color="#009688" dark>Select by Region</v-btn>
						</div>
						<v-divider class="mx-4 mt-3"></v-divider>
					</template>
				</v-autocomplete>

				<v-expand-transition>
					<v-card
						v-show="expand"
						max-height="410"
						width="420"
						class="mx-auto"
					>
						<v-card-title class="title font-weight-regular justify-space-between">
							<span class="mt-n1">{{currentTitle}}</span>
							<v-icon class="mt-n1" @click="expand=false">mdi-close</v-icon>
						</v-card-title>

						<v-divider class="mx-4 mb-2 mt-n1"></v-divider>
						
						<v-window v-model="step">
							<v-window-item :value="1">
								<v-container fluid>
									<v-row>
										<v-col class="text-center my-n3">
											<v-btn color="#009688" dark rounded class="ma-1" v-for="continent in continents" :key="continent" @click="loadCountries(continent)">{{continent}}</v-btn>
										</v-col>
									</v-row>
								</v-container>
							</v-window-item>

							<v-window-item :value="2">
								<v-card flat max-height="200" max-width="400" class="mx-auto pa-2" style="overflow-x: hidden">	
									<v-row>
										<v-col>
											<v-checkbox class="my-n5" :value="country" v-model="selected" v-for="country in continentCountries" :key="country.id" :label="country.text"></v-checkbox>
										</v-col>
									</v-row>
								</v-card>
							</v-window-item>
						</v-window>
						
						<v-divider class="mx-4 mt-2"></v-divider>
						<v-card-actions class="justify-space-between">
							<v-btn :hidden="step === 1" class="mx-n2" text small rounded @click="step = 1">Back</v-btn>
							<v-btn :disabled="step === 1" class="mr-n1" small rounded outlined color="#009688" dark @click="deselectAll">Deselect All</v-btn>
							<v-btn :disabled="step === 1" small rounded color="#009688" dark @click="selectAll">Select All</v-btn>
						</v-card-actions>

					</v-card>
				</v-expand-transition>
			</v-col>
			<v-spacer></v-spacer>
		</v-row>
	</v-container>
</template>

<script>
import countryData from "@/data/countryData"

  export default {
    data: () => ({
			expand: false,
			menu: false,
			step: 1,
			selected: [],
			continentCountries: [],
			selectedContinent: "",
			countries: countryData.countries
		}), 
		computed: {
			continents () {
				let allContinents = this.countries.map(c => c.continent); 
				return [...new Set(allContinents)]; // Set returns all unique values
			},
			currentTitle () {
				if(this.step === 1) {
					return "Select By Region";
				}else {return this.selectedContinent;}
			}
		}, 
		methods: {
			loadCountries: function(continent) {
				this.continentCountries = this.countries.filter(country => country.continent === continent);
				this.step = 2;
				this.selectedContinent = continent;
			},
			selectAll: function() {
				const merged = [...this.selected, ...this.continentCountries];
				this.selected = [...new Set(merged)]; // Set de-duplicates the entries for us
			},
			deselectAll: function() {
				this.selected = this.selected.filter(country => country.continent !== this.selectedContinent);
			},
			showContinentSelector: function() {
				const autocomplete = this.$refs.continentSelector;
				if(autocomplete.isMenuActive) {
					this.$refs.continentSelector.isMenuActive = false;
					autocomplete.blur();
				}
				this.step = 1;
				this.expand = true;
			}
		} 
  }
</script>